.dashboard {
  display: flex;
}

.dashboard__separator {
  background:  
    url(./../img/panelGradientTop.png) no-repeat top,
    url(./../img/panelGradientBottom.png) no-repeat bottom,
    url(./../img/panelVertical5e1f.png);
  height: 100vh;
  width: 16px;
}

.dashboard__space {
  flex-grow: 2;
  background: url(./../img/blackGradientSmallTop.png) repeat-x;
  background-color: darkslategray;
  height: 100vh;
}

.dashboard__store_container {
  overflow-y: scroll;
}


